<template>
  <div class="dataAuditExtrackApply">
    <div class="topTitle">
      <span class="topTitle-span1">客户管理</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">机构客户</span>
      <div class="button-content">
        <Button type="primary" @click="exportFn">
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellTitle">
        <div class="contentCellTitle-1"></div>查询条件
      </div>
      <div class="contentCellBox">
        <div class="contentCellBody1">
          <Form
            ref="formValidate1"
            :model="formValidate1"
            :rules="ruleValidate"
            :label-width="160"
            inline
          >
            <Row>
              <FormItem label="客户号：">
                <Input v-model="formValidate1.val1" placeholder="请输入客户号" style="width:200px"></Input>
              </FormItem>
              <FormItem label="客户名称：">
                <Input v-model="formValidate1.val2" placeholder="请输入客户名称" style="width:200px"></Input>
              </FormItem>
              <FormItem label="证件类型：">
                <Select v-model="formValidate1.val3" style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in idKind"
                    :key="item.code"
                  >{{item.name}}</Option>
                  <!-- <Option value="beijing">New York</Option>
                      <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>-->
                </Select>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="证件号码：">
                <Input v-model="formValidate1.val4" placeholder="请输入证件号" style="width:200px"></Input>
              </FormItem>

              <FormItem label="证件有效期：">
                <DatePicker
                  :value="formValidate1.val5"
                  format="yyyyMMdd"
                  type="daterange"
                  placement="bottom-end"
                  placeholder="请选择时间"
                  style="width: 200px"
                  @on-change="formValidate1.val5=$event"
                ></DatePicker>
              </FormItem>
              <!-- <FormItem label="分支机构：" prop>
                <treeselect
                  v-model="formValidate1.val6"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择"
                  :max-height="100"
                  style="width:200px"
                />
              </FormItem> -->
            <FormItem label="分支机构：">
                <Select v-model="formValidate1.val6" filterable style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in branch"
                    :key="item.code"
                  >{{item.name}}</Option>
                  <!-- <Option value="beijing">New York</Option>
                      <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>-->
                </Select>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="客户状态：">
                <Select v-model="formValidate1.val7" filterable style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in clientStatus"
                    :key="item.code"
                  >{{item.name}}</Option>
                  <!-- <Option value="beijing">New York</Option>
                      <Option value="shanghai">London</Option>
                  <Option value="shenzhen">Sydney</Option>-->
                </Select>
              </FormItem>
              <!-- <FormItem label="客户分类：">
                <Select v-model="formValidate1.val8" filterable multiple style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in organFlag"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem> -->

              <!-- <FormItem label="委托方式：">
                <Select v-model="formValidate1.val9" filterable multiple style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in entrustWay"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem> -->
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" style="width:89px" @click="searchFun">查询</Button>
                <!-- <Button type="primary" style="margin-left:10px">精确查询</Button> -->
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader1">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <!-- ++++++++++ -->
                <tr v-for="(item,index) in tableBody1">
                   <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td>
                      <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.BRANCH_NO"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.BRANCH_NO}}</p>
                    </el-popover>
                    </td>
                  </td>
                  <td class="spanBox" @click="CLIENTFn(item.CLIENT_ID)">{{item.CLIENT_ID}}</td>
                  <td>
                    <!-- {{item.CLIENT_NAME}} -->
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.CLIENT_NAME"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.CLIENT_NAME}}</p>
                    </el-popover>
                    </td>
                  <!-- 客户姓名 -->
                  <td>{{item.CLIENT_STATUS}}</td>
                  <!-- 客户状态 -->
                  <td>
                      <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.ID_KIND"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.ID_KIND}}</p>
                    </el-popover>
                  </td>
                  <!-- 证件类型 -->
                  <td>{{item.ID_NO}}</td>
                  <!-- 证件号码 -->
                  <td>{{item.ID_BEGINDATE}}</td>
                  <!-- 证件有效期起来 -->
                  <td>{{item.ID_ENDDATE}}</td>
                  <!-- 证件有效期结束 -->

                  <td>{{item.OPEN_DATE}}</td>
                  <!-- 开户日期 -->
                  <td>{{item.CANCEL_DATE}}</td>
                  <!-- 销户日期 -->
                  <td>{{item.ORGAN_FLAG}}</td>
                  <!-- /是否机构户标志 -->
                  <!-- --- -->
                  <td>{{item.PROFESSION_CODE}}</td>
                  <!-- 行业代码 -->
                  <!-- <td>{{item.DEGREE_CODE}}</td> -->
                  <!-- 学历 -->
                  <!-- <td>{{item.CLIENT_SEX}}</td> -->
                  <!-- 用户性别 -->
                  <td>{{item.NATIONALITY}}</td>
                  <!-- 国籍 -->
                  <td>
                    <!-- {{item.ID_ADDRESS}} -->
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.ID_ADDRESS"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.ID_ADDRESS}}</p>
                    </el-popover>
                  </td>
                  <!-- 证件地址 -->
                  <td>
                    <!-- {{item.ADDRESS}} -->
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.ADDRESS"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.ADDRESS}}</p>
                    </el-popover>
                  </td>
                  <!-- 地址 -->
                  <td>{{item.PHONECODE}}</td>
                  <!-- 电话号码 -->
                  <td>{{item.MOBILETELEPHONE}}</td>
                  <!-- 手机号码 -->
                  <td>{{item.FAX}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
  created() {
    this.tableList();
    let that = this;
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.selectTreeCleanUp(response.data);
          that.data1 = response.data;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/idKind")
      .then(response => {
        if (response.code == "200") {
          that.idKind = response.data.idKind;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/clientStatus")
      .then(response => {
        if (response.code == "200") {
          that.clientStatus = response.data.clientStatus;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/branch")
      .then(response => {
        if (response.code == "200") {
          that.branch = response.data.branch;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/entrustWay")
      .then(response => {
        if (response.code == "200") {
          that.entrustWay = response.data.entrustWay;
        }
      })
      .catch(error => {});
    this.axios
      .get("/system/getDic/organFlag")
      .then(response => {
        if (response.code == "200") {
          that.organFlag = response.data.organFlag;
        }
      })
      .catch(error => {});
  },

  data() {
    return {
      organFlag: [],
      entrustWay: [],
      clientStatus: [],
      idKind: [],
      data1: [],
      pageSize: 10,
      total: 0,
      pageNum: 1,
      tabname: "",
      formValidate1: {
        val1: "",
        val2: "",
        val3: '',
        val4: "",
        val5: ["",""],
        val6: "",
        val7: "",
        val8: "",
        val9: ""
      },
      // formValidate2: {
      //   val1: "",
      //   val2: "",
      //   val3: "",
      //   val4: "",
      //   val5: "",
      //   val6: "",
      //   val7: "",
      //   val8: "",
      //   val9: ""
      // },
      // formValidate3: {
      //   val1: "",
      //   val2: "",
      //   val3: "",
      //   val4: "",
      //   val5: "",
      //   val6: "",
      //   val7: "",
      //   val8: "",
      //   val9: "",
      //   val10: "",
      //   val11: ""
      // },
      ruleValidate: {},
      tableHeader1: [
        { name: "序" },
        { name: "分支机构" },
        { name: "客户号" },
        { name: "客户姓名" },
        { name: "客户状态" },
        { name: "证件类型" },
        { name: "证件号码" },
        { name: "证件有效期起始日期" },
        { name: "证件有效期到期日期" },
        { name: "开户日期" },
        { name: "销户日期" },
        { name: "是否机构户标志" },
        { name: "行业代码" },
        // { name: "学历" },
        // { name: "用户性别" },
        { name: "国籍" },
        { name: "证件地址" },
        { name: "地址" },
        { name: "电话号码" },
        { name: "手机号码" },
        { name: "传真" }
        // ------------
      ],
      // tableHeader2: [
      //   { name: "序" },
      //   { name: "客户号" },
      //   { name: "客户名称" },
      //   { name: "账户类型" },
      //   { name: "证券账号" },
      //   { name: "账户状态" },
      //   { name: "开户方式" },
      //   { name: "开户日期" },
      //   { name: "销户日期" },
      //   { name: "所属业务条线" }
      // ],
      // tableHeader3: [
      //   { name: "序" },
      //   { name: "客户号" },
      //   { name: "客户名称" },
      //   { name: "账户类别" },
      //   { name: "账户标识" },
      //   { name: "限制措施" },
      //   { name: "限制开始时间" },
      //   { name: "资金账户" },
      //   { name: "账户状态" },
      //   { name: "开户日期" },
      //   { name: "销户日期" },
      //   { name: "开户方式" },
      //   { name: "代理人姓名" },
      //   { name: "代理人省份证件类型" },
      //   { name: "代理人身份证件类型说明" },
      //   { name: "代理人身份证件号码" },
      //   { name: "代理人身份证件号码有效期" },
      //   { name: "银行账户类型" },
      //   { name: "银行结算账号" },
      //   { name: "银行账户名称" },
      //   { name: "结算银行代码" },
      //   { name: "结算银行名称" },
      //   { name: "结算银行所在地" },
      //   { name: "管理部门" },
      //   { name: "来源系统编码" }
      // ],
      tableBody1: []
      // tableBody2: [
      //   {
      //     id: "",
      //     val1: "err001",
      //     val2: "张宏",
      //     val3: "11：沪市A股账户",
      //     val4: "62656772653573776363",
      //     val5: "正常",
      //     val6: "临柜开户",
      //     val7: "1988-10-12",
      //     val8: "2019-10-12",
      //     val9: "零售运维管理部"
      //   }
      // ],
      // tableBody3: [
      //   {
      //     id: "",
      //     val1: "er001",
      //     val2: "张红",
      //     val3: "11:普通资金账户",
      //     val4: "11:主账户",
      //     val5: "",
      //     val6: "",
      //     val7: "6557890009767",
      //     val8: "正常",
      //     val9: "20181212",
      //     val10: "",
      //     val11: "11:临柜开户",
      //     val12: "张菲",
      //     val13: "01：居民身份证",
      //     val14: "",
      //     val15: "131898778787878",
      //     val16: "2018379",
      //     val17: "基本户",
      //     val18: "626566636636",
      //     val19: "张菲",
      //     val20: "",
      //     val21: "",
      //     val22: "",
      //     val23: "零售运维管理部",
      //     val24: "EGFDG系统",

      //   }
      // ]
    };
  },
  methods: {
                download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn(){
      let that = this;
      let data = {
        organ_flag:'1',
        CLIENT_ID:this.formValidate1.val1,
        CLIENT_NAME:this.formValidate1.val2,
        ID_KIND:this.formValidate1.val3,
        ID_NO:this.formValidate1.val4,
        //ID_BEGINDATE:this.formValidate1.val5[0],
        ID_ENDDATE:JSON.stringify(this.formValidate1.val5),
        BRANCH_NO:this.formValidate1.val6,
        CLIENT_STATUS:this.formValidate1.val7,
      };
      data = JSON.stringify(data);
      this.axios
        .post(`/client/patch/list/export/${this.pageNum}/${this.pageSize}`, data, {
         headers: { "Content-Type": "application/json" },
          responseType: "blob"
        })
        .then(response => {
          this.download(response,'导出文档.xls');
        })
        .catch(error => {});
    },
        CLIENTFn(a) {
      let routeData = this.$router.resolve({
        path: "/baseinfo",
        query: { CLIENT_ID: a, type: "1" }
      });
      window.open(routeData.href, "_blank");
    },
    searchFun(){
      this.pageNum=1;
      this.tableList();
    },
    pageChangeFun(a){
       this.pageNum=a*1;
      this.tableList();
    },
    pageSizeChange(a){
      this.pageSize=a*1;
      this.tableList();
    },
    tableList() {
      let data = {
        organ_flag:'1',
        CLIENT_ID:this.formValidate1.val1,
        CLIENT_NAME:this.formValidate1.val2,
        ID_KIND:this.formValidate1.val3,
        ID_NO:this.formValidate1.val4,
        //ID_BEGINDATE:this.formValidate1.val5[0],
        ID_ENDDATE:JSON.stringify(this.formValidate1.val5),
        BRANCH_NO:this.formValidate1.val6,
        CLIENT_STATUS:this.formValidate1.val7,
      };
      console.log(data);
      data=JSON.stringify(data)
      this.axios
        .post(`/client/patch/list/${this.pageNum}/${this.pageSize}`, data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.tableBody1 = response.data.content;
            this.total=response.data.totalSize;
          }
        })
        .catch(error => {});
    },
    selectTreeCleanUp(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children && treeDatas[i].children.length > 0) {
          this.selectTreeCleanUp(treeDatas[i].children);
        } else this.$delete(treeDatas[i], "children");
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.dataAuditExtrackApply {
  .rightTopbtn {
    margin: 0px 5px;
    i.ivu-icon {
      font-size: 16px;
      vertical-align: -3px;
      padding-right: 2px;
    }
  }
  .btnClass {
    display: flex;
    justify-content: center;
    padding: 20px 0;
  }
  .operationSpan {
    color: blue;
    margin: 0 5px;
    cursor: pointer;
  }
}
</style>



